import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { ICotizacion, ICotizacionItem, ICreateGenerarOC, IItemPedidoForm, IPedidoDePresupuesto } from '../../models/pedido-de-presupuesto';
import { PedidoDePresupuestoService } from '../../service/pedidos-de-presupuesto.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { combineLatest, finalize, firstValueFrom, map, Observable, startWith, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';
import { DatePipe } from '@angular/common';
import { MatTableDataSource } from '@angular/material/table';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { IAlertConfig } from '@shared/models/alert';

// Interfaz que maneja el modal de dialog
interface DataModal {
    pedido_de_presupuesto: {},
    cotizacion: {}
}

@Component({
    selector: 'app-generar-orden-pedido-de-presupuesto',
    templateUrl: './generar-orden.component.html',
    styleUrls: ['./generar-orden.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})

export class GenerarOrdenComponent implements OnInit {
    // Necesarios para la app
    showAlert = false;
    _alertConfig: IAlertConfig;
    title: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    maxPalabras: number = 50;
    minDate: Date;
    maxDate: Date;

    @Output() public creadoExitosamente: EventEmitter<{ data?}> = new EventEmitter();
    @Output() public editadoExitosamente: EventEmitter<{ data?}> = new EventEmitter();

    // Particulares del modulo
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    cotizacion: ICotizacion
    pedido_de_presupuesto: IPedidoDePresupuesto;
    filteredProveedores: Observable<IProveedor[]>;
    form: FormGroup;
    proveedores: IProveedor[];
    columns = ['codigointerno', 'articulo', 'solicitado', 'detalle', 'cotiza', 'cantidad', 'unitario', 'subtotal', 'oc']; // estos son las columnas de la grilla
    items_formulario: IItemPedidoForm[];
    isReadOnly: boolean = true; // Cambiar a false si quieres permitir edición

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: DataModal,
        private _pedidoDePresupuestoService: PedidoDePresupuestoService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        private datePipe: DatePipe,
        public dialogRef: MatDialogRef<GenerarOrdenComponent>,
        private dialogo: MatDialog,
    ) {
        this.pedido_de_presupuesto = this.data.pedido_de_presupuesto ? this.data.pedido_de_presupuesto : null;
        this.cotizacion = this.data.cotizacion ? this.data.cotizacion : null;
        this.title = `Generar Orden de compra a partir de la Cotización #${this.cotizacion.idcotizacion} - ${this.cotizacion.proveedor}`;
        // Se define minDate como null o cualquier otra fecha límite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    ngOnInit(): void {
        this.newForm();
        this.getProveedores(); // Método que carga proveedores
        this.subscribeAlert();
        this.existeCotizacion();
    }

    /**
     * Metodo que inicializa el/los formularios de la aplicaciónz
     */
    newForm() {
        const [dia, mes, anio] = this.cotizacion ? this.cotizacion.fecha.split('/') : ''; //Se hace esto para poder manejar el formato que se cambio en el render del componente de tabla dinamica.

        this.form = new FormGroup({
            proveedor: new FormControl(({ Codigo: this.cotizacion.idproveedor, RazonSocial: this.cotizacion.proveedor }), [Validators.required]),
            fecha: new FormControl((this.datePipe.transform(new Date(`${mes}/${dia}/${anio}`), 'yyyy-MM-dd')), [Validators.required]),
            total: new FormControl(''),
            observaciones: new FormControl((this.cotizacion.observaciones), [Validators.maxLength(this.maxPalabras)]),
        });

        if (this.isReadOnly) {
            this.form.disable();
        }
    }

    /**
     * Metodo encargado de encapsular los datos en un payload y realizar la persistencia
     */
    onSubmit() {
        const existeOrdenCompra = this.items_formulario.find(i => i.oc.value === true);
        if (!existeOrdenCompra) {
            return this.alert.warning('Debe marcar por lo menos un item para generar la orden de compra');
        }

        const payload: ICreateGenerarOC = {
            cotizacion: this.cotizacion.idcotizacion
        }

        if (this.cotizacion?.idcotizacion) {
            this._pedidoDePresupuestoService.generarOrdenCompra(payload)
                .subscribe({
                    next: (data: any) => {
                        if (data) {
                            this.creadoExitosamente.emit(data);
                            this.onCloseMenu(data);
                        }
                    }
                });
        }
    }

    /**
     * Metodo encargado de cerrar el dialogo, transmitiendo la informacion al componente padre
     * @param data 
     */
    onCloseMenu(data) {
        this.dialogRef.close(data)
    }

    /**
     * Metodo que se encarga de verificar que exista cotizacion para mapear los datos del servicio que lista los items de una cotizacion
     */
    async existeCotizacion() {
        let Items; // Variable auxiliar

        if (this.cotizacion?.idcotizacion) {
            Items = await firstValueFrom(this._pedidoDePresupuestoService.getItemsCotizacion(this.cotizacion.idcotizacion));
        }

        Items.list = Items.list.map(i => {
            return {
                ...i,
                codigo_interno: i.codigo_articulo,
                oc: i.oc === 1 ? true : false,
                cotiza: i.cotiza === 1 ? true : false,
                cantidad: Number(i.cantidad),
                unitario: Number(i.unitario),
                subtotal: Number(i.subtotal)
            }
        })

        await this.crearGrillaForm(Items);
    }

    /**
     * Metodo encargado de
     * 1) Recorrer los items que me devuelve la API para agregar un input con el valor en "cantidad recibida", valor "unitario", "subtotal", "OC" y "Cotiza".
     * 2) Escuchar los cambios de "cantidad recibida" y valor "unitaro" para calcular el subtotal y total.
     * 3) Actualizar el campo "total" y rendizar los items en la grilla.
     * @param Items 
     */
    crearGrillaForm(Items) {
        const subtotalObservables = []; // Almacena los observables de subtotales
        this.items_formulario = Items.list;

        this.items_formulario.forEach((item: IItemPedidoForm | ICotizacionItem, index, items_array) => {

            // Define los FormControls
            const cantidadRecibidaControl = new FormControl(
                { value: item.cantidad, disabled: this.isReadOnly },
                [Validators.required, Validators.min(0)]
            );
            const unitarioControl = new FormControl(
                { value: item.unitario || null, disabled: this.isReadOnly },
                [Validators.required, Validators.min(0)]
            );
            const subtotalControl = new FormControl(
                { value: item.subtotal || null, disabled: this.isReadOnly },
                [Validators.required, Validators.min(0)]
            );

            // Escuchar cambios en cantidad_recibida y unitario
            cantidadRecibidaControl.valueChanges.subscribe(() => {
                this.updateSubtotal(cantidadRecibidaControl, unitarioControl, subtotalControl);
                this.updateTotal()
            });
            unitarioControl.valueChanges.subscribe((a) => {
                this.updateSubtotal(cantidadRecibidaControl, unitarioControl, subtotalControl);
                this.updateTotal()
            });

            subtotalObservables.push(
                subtotalControl.valueChanges.pipe(
                    startWith(Number(subtotalControl.value) || 0)
                )
            );

            // Asigna los controles al item
            items_array[index].cantidad_recibida = cantidadRecibidaControl;
            items_array[index].unitario = unitarioControl;
            items_array[index].subtotal = subtotalControl;

            items_array[index].cotiza = new FormControl({ value: item.cotiza, disabled: this.isReadOnly });  // No es necesario un validador aquí si es booleano
            items_array[index].oc = new FormControl({ value: item.oc, disabled: this.isReadOnly });     // No es necesario un validador aquí si es booleano
        });

        this.updateTotal()


        combineLatest(subtotalObservables).pipe(
            map((subtotales: number[]) => {
                return subtotales.reduce((sum, current) => sum + (current || 0), 0);
            })
        ).subscribe((total) => {
            this.form.get('total')?.patchValue(total, { emitEvent: false });
        });

        this.dataSource = new MatTableDataSource(this.items_formulario);
    }

    // ################# Metodos de comunicacion con el service #################

    /**
     * Metodo que consulta los proveedores del sistema y ademas encapsula los proveedores de que tiene un pedido de presupuesto
     * @param option 
     */
    async getProveedores(option?) {
        const Proveedores = await firstValueFrom(this._pedidoDePresupuestoService.getProveedores(option));
        const proveedores_del_pedido = this.pedido_de_presupuesto.proveedores.split(',').map(num => parseInt(num));

        new Promise(async (resolve) => {
            this.proveedores = Proveedores.list.filter(p => proveedores_del_pedido.includes(p.Codigo));
            this.filteredProveedores = this.form.get('proveedor').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterProveedores(value)),
            );
            resolve(true);
        })
    };

    /**
     * Metodo de filtrado utilizado en los autocompletes
     * @param proveedor 
     * @returns 
     */
    private _filterProveedores(proveedor: IProveedor): IProveedor[] {
        const filterValue = proveedor.toString().toLowerCase();
        return this.proveedores.filter(proveedor => proveedor.RazonSocial.toLowerCase().includes(filterValue));
    }

    /**
     * Metodo para renderizar el texto de un autocomplete
     * @param option 
     * @returns 
     */
    getTextProveedor(option) {
        return option ? option.RazonSocial : '';
    }

    // ################# Metodos extra #################

    /**
     * Metodo para calcular el valor total entre todos los subtotales de una grilla
     */
    updateTotal() {
        const total = this.items_formulario.reduce((sum, item) => {
            const subtotal = Number(item.subtotal['value']) || 0;
            return sum + subtotal;
        }, 0);

        this.form.get('total')?.setValue(total, { emitEvent: false });
    }


    /**
    * Método para calcular y actualizar el subtotal
    */
    updateSubtotal(
        cantidadRecibidaControl: FormControl,
        unitarioControl: FormControl,
        subtotalControl: FormControl
    ) {
        const cantidad = Number(cantidadRecibidaControl.value) || 0;
        const unitario = Number(unitarioControl.value) || 0;
        const subtotal = cantidad * unitario;

        subtotalControl.setValue(subtotal, { emitEvent: false }); // Evita bucles de eventos
    }


    /**
    * Funcion para contar las palabras en las observaciones
    */
    getPalabrasLength(): number {
        const observaciones = this.form.get('observaciones').value;
        return observaciones ? observaciones.length : 0;
    }

    /**
    * Funcion para verificar si se ha alcanzado el límite de caracteres en las observaciones
    */
    isPalabraMaxLengthReached(): boolean {
        return this.getPalabrasLength() <= this.maxPalabras;
    }

    cambiarFecha(fecha: string) {
        // Supongamos que la fecha está en el formato 'DD-MM-YYYY'
        const partes = fecha.split('/');
        if (partes.length === 3) {
            const anio = partes[0];
            const mes = partes[1];
            const dia = partes[2];
            const fh = new Date(Number(anio), Number(mes), Number(dia))
            return fh;
        } else {
            // Manejar el caso en que la cadena no tiene el formato esperado
            return 'Fecha inválida';
        }
    }

}