import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IPedidoDePresupuesto } from '../models/pedido-de-presupuesto';
import { PedidoDePresupuestoService } from '../service/pedidos-de-presupuesto.service';
import { Subject, Subscription, debounceTime, distinctUntilChanged, firstValueFrom, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { SaveComponent } from './save/save.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { CotizarComponent } from './cotizar/cotizar.component';
import { VerCotizacionComponent } from './ver-cotizacion/ver-cotizacion.component';
import { PwaService } from '@shared/service/app/pwa.service';
import { EnviarEmailModalComponent } from '@shared/components/enviar-email-modal/enviar-email.component';

@Component({
    selector: 'app-pedidos-de-presupuesto',
    templateUrl: './pedidos-de-presupuesto.component.html',
    styleUrls: ['./pedidos-de-presupuesto.component.scss']
})

export class PedidoDePresupuestoComponent implements OnInit, OnDestroy {
    pedidos_de_presupuesto: IPedidoDePresupuesto[];
    public dataSource: MatTableDataSource<IPedidoDePresupuesto> = new MatTableDataSource();
    headers = ['Numero', 'Fecha', 'Contestado', 'Proveedores', 'Acciones'];
    column_params = ['idpedido', 'fecha', 'cotizado', 'proveedores_descripcion', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['proveedores_descripcion'];
    filter: IFilter = {
        cotizado: -1,
        proveedor: -1,
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.compras.view_compras_pedidos + '?';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    @Output() public enviadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    constructor(
        private _pedidoDePresupuestosService: PedidoDePresupuestoService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private router: Router,
        public _headerTextService: HeaderTextService,
        private pwaService: PwaService
    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [{
            icono: 'view',
            nombre_boton: "Ver pedido",
            functionName: 'ver',
            iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:ver',
            iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
            iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
        },
        {
            icono: 'edit',
            nombre_boton: "Editar",
            functionName: 'editar',
            iconoAccion: (pedido: IPedidoDePresupuesto) => pedido.cotizado === 0 ? 'jedstion:editar' : 'jedstion:editar_disabled',
            iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
            iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
        },
        {
            icono: 'delete',
            nombre_boton: "Eliminar",
            functionName: 'eliminar',
            iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:eliminar',
            iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
            iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
        },
        {
            icono: 'comment',
            nombre_boton: "Cotizaciones",
            functionName: 'cotizaciones',
            iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:cotizar',
            iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
            iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '' /* 'jedstion:cotizar_disabled' */,
        },
        {
            icono: 'impresora',
            nombre_boton: "Reporte de Pedido",
            functionName: 'reporte_pedido',
            iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:imprimir',
            iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
            iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
        },
        {
            icono: 'send',
            nombre_boton: "Enviar Pedido",
            functionName: 'enviar_pedido',
            iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:enviar',
            iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
            iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
        }
        ];

        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {

        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(750),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.cotizado != -1 ? "filter[cotizado]=" + this.filter.cotizado : '';
                        this.extraParams += this.filter.proveedor != -1 ? "&filter[proveedores][like]=" + this.filter.proveedor.Codigo.toString() : '';
                        this.tabla.filters(this.filtroBusqueda);
                    }
                },
            });
    }

    /**
 * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
 * @param event 
 */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                if (event.params.data.cotizado === 0) {
                    this.updatePedidoDePresupuesto(event);
                } else {
                    this.alert.warning('Este pedido no puede editarse. Tiene una cotización asociada')
                }
                break;
            case 'eliminar':
                this.deletePedidoDePresupuesto(event);
                break;
            case 'cotizaciones':
                this.cotizarPedidoDePresupuesto(event);
                break;
            case 'ver':
                this.verPedidoDePresupuesto(event);
                break;
            case 'reporte_pedido':
                this.reportePedidoDePresupuesto(event);
                break;
            case 'enviar_pedido':
                // this.alert.info('Esta función estará disponible proximamente');
                this.enviarPedidoDePresupuesto(event);
                break;
        }
    }

    /**
     * Esta funcion guarda un item
     */
    savePedidoDePresupuesto() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .afterClosed()
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    updatePedidoDePresupuesto(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .afterClosed()
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deletePedidoDePresupuesto(event) {
        this.confirm.open({
            title: "Eliminar pedido de presupuesto " + event.params.data.observaciones,
            message: "¿Desea eliminar el pedido de presupuesto: " + event.params.data.observaciones + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._pedidoDePresupuestosService.deletePedidoDePresupuesto(event.params.data.idpedido).subscribe({
                        next: (data) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Pedido de presupuesto eliminado con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.error("Este pedido de presupuesto no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    cotizarPedidoDePresupuesto(event) {
        this.dialogo
            .open(CotizarComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .afterClosed()
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    verPedidoDePresupuesto(event) {
        this.dialogo
            .open(VerCotizacionComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .afterClosed()
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion Genera un Reporte y lo muestra en una nueva pestaña
     */
    reportePedidoDePresupuesto(event) {
        const idpedido = event.params.data.idpedido;
        this._pedidoDePresupuestosService.reportePedidoDePresupuesto(idpedido).subscribe(
            (data) => {
                if (data.success) {
                    const reportePath = data.path;
                    if (this.pwaService.isPwa()) {
                        this.router.navigate(['/reporte', reportePath]);
                    } else {
                        const reporteUrl = `${environment.baseRest}/${reportePath}`;
                        window.open(reporteUrl, '_blank');
                    }
                } else {
                    console.error('Error al generar el reporte');
                }
            },
            (error) => {
                console.error('Error en la solicitud del reporte', error);
            }
        );
    }

    enviarPedidoDePresupuesto(event) {
        const modal_data = {
            model: event.params.data,
            _endpointService: environment.compras.compras_pedidos_enviar_presupuesto,
            title: `Enviar Pedido de Presupuesto N°${event.params.data.idpedido}`,
            permiteAdjunto: false
        };

        this.dialogo
            .open(EnviarEmailModalComponent, { data: modal_data, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.enviadoExitosamente
            .subscribe({
                next: () => {
                        this.alert.success('Email enviado con éxito');
                        this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe()
    }
}