import { Component, EventEmitter, Inject, OnInit, Output, ViewChild } from '@angular/core';
import { ICotizacion, ICreateCotizacion, IPedidoDePresupuesto } from '../../models/pedido-de-presupuesto';
import { PedidoDePresupuestoService } from '../../service/pedidos-de-presupuesto.service';
import { FormGroup } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { finalize, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { MatTableDataSource } from '@angular/material/table';
import { environment } from 'environments/environment';
import { NuevaCotizacionComponent } from '../nueva-cotizacion/nueva-cotizacion.component';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { IAlertConfig } from '@shared/models/alert';
import { IResponse } from '@shared/models/response-with-token';
import { GenerarOrdenComponent } from '../generar-orden/generar-orden.component';

@Component({
    selector: 'app-cotizar-pedidos-de-presupuesto',
    templateUrl: './cotizar.component.html',
    styleUrls: ['./cotizar.component.scss']
})
export class CotizarComponent implements OnInit {
    // Necesarios para la app
    showAlert = false;
    _alertConfig: IAlertConfig
    title: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    // Particulares del modulo
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    headers = ['Fecha', 'Proveedor', 'Acciones'];
    column_params = ['fecha', 'proveedor', 'acciones'];
    form: FormGroup;
    pedido_de_presupuesto: IPedidoDePresupuesto;
    funcionesObjeto = null;
    cotizaciones: ICotizacion[];
    env = null;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _pedidosDePresupuestosService: PedidoDePresupuestoService,
        private confirm: FuseConfirmationService,
        private _empresaService: EmpresaService,
        private dialogo: MatDialog,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<CotizarComponent>
    ) {
        this.pedido_de_presupuesto = this.data ? this.data : null;
        this.title = `Cotizaciones Pedido de Presupuesto N° ${this.pedido_de_presupuesto.idpedido}`;

        /**
     * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
     */
        this.funcionesObjeto = [
            {
                icono: 'edit',
                nombre_boton: "Editar",
                functionName: 'editar',
                iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:editar',
                iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
                iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
            },
            {
                icono: 'delete',
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
                iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:eliminar',
                iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
                iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
            },
            {
                icono: 'orden_de_compra',
                nombre_boton: "Generar Orden de Compra",
                functionName: 'orden_de_compra',
                iconoAccion: (pedido: IPedidoDePresupuesto) => 'jedstion:orden_de_produccion',
                iconoAccionado: (pedido: IPedidoDePresupuesto) => '',
                iconoDeshabilitado: (pedido: IPedidoDePresupuesto) => '',
            }
        ];

        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        this.env = environment.compras.view_compras_cotizacion + '?' + `filter[idpedido]=${this.pedido_de_presupuesto.idpedido}&sort=-fecha`;
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;

    ngOnInit(): void {
        // Load empresa data
        this._empresaService.getEmpresa();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    onCloseMenu() {
        this.dialogRef.close()
    }

    /**
    * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
    * @param event 
    */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                this.updateCotizacion(event);
                break;
            case 'eliminar':
                this.deleteCotizacion(event);
                break;
            case 'orden_de_compra':
                this.generarOrdenDeCompra(event);
                break;
        }
    }

    nuevaCotizacion() {
        this.dialogo
            .open(NuevaCotizacionComponent, {
                data: {
                    pedido_de_presupuesto: this.pedido_de_presupuesto,
                    cotizacion: null
                },
                panelClass: 'my-dialog',
                autoFocus: false
            })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Cotización creada con éxito.");
                    this.tabla.renderTabla();
                },
            });
    }

    /** Modal Encargado de levantar edicion */
    updateCotizacion(event) {
        this.dialogo
            .open(NuevaCotizacionComponent, {
                data: {
                    pedido_de_presupuesto: this.pedido_de_presupuesto,
                    cotizacion: event.params.data
                },
                panelClass: 'my-dialog',
                autoFocus: false
            })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: (data: IResponse<any>) => {
                    if (data.success) {
                        this.alert.success("Cotización editada con éxito.");
                    } else if (!data.success) {
                        this.alert.error(data.result);
                    }

                    this.tabla.renderTabla();
                }
            })
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteCotizacion(event) {
        this.confirm.open({
            title: "Eliminar Cotización " + event.params.data.observaciones,
            message: "¿Desea eliminar la Cotización de: " + event.params.data.proveedor + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._pedidosDePresupuestosService.deleteCotizacion(event.params.data.idcotizacion).subscribe({
                        next: (data: any) => {
                            // Manejar la respuesta exitosa
                            if (data.success) {
                                this.alert.success("Cotización eliminada con éxito.");
                                this.tabla.renderTabla()
                            } else {
                                this.alert.error(data.result);
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.error("Este pedido de presupuesto no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    /**
     * Metodo encargado levantar el modal de orden de compra
     * @param event 
     */
    generarOrdenDeCompra(event) {
        this.dialogo
            .open(GenerarOrdenComponent, {
                data: {
                    pedido_de_presupuesto: this.pedido_de_presupuesto,
                    cotizacion: event.params.data
                },
                panelClass: 'my-dialog',
                autoFocus: false
            })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: (data: IResponse<any>) => {
                    if (data.success) {
                        this.alert.success("Orden de compra Generada con éxito.");
                    } else if (!data.success) {
                        this.alert.error(data.result);
                    }

                    this.tabla.renderTabla();
                }
            })
    }
}